  <v-app>
    <v-content>
      <v-card height="100vh" flat class="pa-0 d-flex flex-column justify-start">
        <v-toolbar dark dense class="flex-grow-0" elevation="1" color="primary">
          <v-toolbar-title>CIRCULAR QUEUE</v-toolbar-title>
          <template v-slot:extension>
            <v-row>
              <v-col class="sm-6">
                <v-tabs background-color="primary" v-model="tab1">
                  <v-tab href="#theory-tab" class="text-capitalize"
