                  >
                  <v-tab href="#code-tab" class="text-capitalize">Code</v-tab>
                  <v-tab href="#playin-tab" class="text-capitalize"
                    >Playground IN</v-tab
                  >
                </v-tabs>
              </v-col>
              <v-col class="sm-6">
                <v-tabs background-color="primary" centered v-model="tab2">
                  <v-tab href="#playout-tab" class="text-capitalize"
                    >Playground OUT</v-tab
                  >
                </v-tabs>
              </v-col>
            </v-row>
          </template>
        </v-toolbar>
        <v-card flat class="flex-grow-1">
          <v-row no-gutters class="fill-height">
            <v-col sm="6">
              <v-card height="100%" flat class="pa-2">
                <v-tabs-items v-model="tab1">
                  <v-tab-item :value="'theory-tab'">Theory</v-tab-item>
                  <v-tab-item :value="'code-tab'">
                    <v-card
                      class="pa-4 overflow-y-auto overflow-x-auto fill-height"
                    >
                      <pre id="code">
                        {{
                          `
class CircularQueue {
  constructor(s) {
    this.maxSize = s;
    this.items = [];
    this.front = 0;
    this.back = -1;
    this.queueSize = 0;

    for (let i = 0; i < s; i++) {
      this.items[i] = null;
    }
  }

  enqueue(item) {
    if (this.isFull()) throw new Error("Overflow error");

    if (this.back == this.maxSize - 1) this.back = -1;
    this.items[++this.back] = item;
    this.queueSize++;
  }

  dequeue() {
    if (this.isEmpty()) throw new Error("Underflow error");

    let temp = this.items[this.front++];
    this.items[this.front - 1] = null;
    if (this.front == this.maxSize) this.front = 0;
    this.queueSize--;
    return temp;
  }

  peek() {
    if (this.isEmpty()) throw new Error("Empty Queue");

    return this.items[this.front];
  }

  isEmpty() {
    return this.queueSize == 0;
  }

  isFull() {
    return this.queueSize == this.maxSize;
  }

  size() {
    return this.queueSize;
  }

  getItemsAsArray() {
    return this.items;
  }
}
`
                        }}
                      </pre>
                    </v-card>
                  </v-tab-item>
                  <v-tab-item :value="'playin-tab'">
                    <v-card flat class="pa-4">
                      <v-text-field
                        @keyup.enter="createQueue"
                        @focusout="createQueue"
                        label="MAX SIZE"
                        outlined
                        v-model="maxQueueSize"
                      ></v-text-field>
                      <v-card flat class="d-flex justify-end">
                        <v-btn
                          @click="createQueue"
                          color="primary"
                          class="text-none"
                          >CREATE QUEUE</v-btn
                        >
                      </v-card>
                    </v-card>
                    <v-divider></v-divider>
                    <v-card
                      flat
                      class="d-flex flex-wrap justify-space-around pa-4"
                    >
                      <v-btn
                        @click="dequeue"
                        color="primary"
                        class="text-none mt-3"
                        >dequeue()</v-btn
                      >
                      <v-btn
                        @click="peek"
                        color="primary"
                        class="text-none mt-3"
                        >peek()</v-btn
                      >
                      <v-btn
                        @click="allItems"
                        color="primary"
                        class="text-none mt-3"
                        >allItems()</v-btn
                      >
                      <v-btn
