                        class="text-none mt-3"
                        >isEmpty()</v-btn
                      >
                      <v-btn
                        @click="isFull"
                        color="primary"
                        class="text-none mt-3"
                        >isFull()</v-btn
                      >
                    </v-card>

                    <v-card flat class="pa-4">
                      <v-text-field
                        @keyup.enter="enqueue"
                        label="Item To Add"
                        outlined
                        v-model="item"
                      ></v-text-field>
                      <v-card flat class="d-flex justify-end">
                        <v-btn
                          @click="enqueue"
                          color="primary"
                          class="text-none"
                          >enqueue(item)</v-btn
                        >
                      </v-card>
                    </v-card>
                  </v-tab-item>
                </v-tabs-items>
              </v-card>
            </v-col>
            <v-col sm="6" flat outlined class="fixed">
              <v-card
                outlined
                ref="outputView"
                flat
                class="d-flex flex-column fill-height overflow-y-auto px-4"
              >
                <v-toolbar flat dense class="flex-grow-0 pa-0">
                  <!-- <v-chip small class="mx-auto">Output</v-chip> -->
                  <v-btn @click="output = []" color="info" class="ml-auto"
                    >CLEAR OUTPUT</v-btn
                  >
                </v-toolbar>
                <v-card
                  flat
                  class="d-flex flex-column-reverse justify-end fill-height overflow-y-auto"
                >
                  <template>
                    <div v-for="(data, index) in output" :key="index">
                      <v-card flat class="d-flex mb-3">
                        <v-card class="d-flex">
                          <v-card
                            flat
                            dark
                            :color="data.isError ? 'error' : 'primary'"
                            class="px-4 py-1"
                          >
                            {{ data.operation }}
                          </v-card>
                          <v-card
                            v-if="
                              data.result !== undefined &&
                                data.operation !== 'allItems()'
                            "
                            flat
                            class="px-10 d-flex justify-center align-center"
                          >
                            {{ data.result }}
                          </v-card>
                        </v-card>
                      </v-card>
                      <v-card
                        flat
                        class="d-flex mb-3 flex-wrap"
                        v-if="data.operation === 'allItems()'"
                      >
                        <!-- <v-card dark color="success" class="pa-2 mb-3 mr-3">
                          Back
                        </v-card>
                        <v-card
                          v-if="data.result.length === 0"
                          class="pa-2 mb-3 mr-3 px-4"
                        >
                          -
                        </v-card> -->
                        <v-card
                          flat
                          v-for="(item, index) in data.result"
                          :key="index"
                        >
                          <!-- {{ item || "-" }}
                          {{
                            index == data.front && !data.isEmpty
                              ? " (FRONT)"
                              : ""
                          }}
                          {{ index == data.back ? " (BACK)" : "" }} -->
                          <v-card
                            v-if="index == data.front && !data.isEmpty"
                            class="d-flex mb-3 mr-3"
                          >
                            <v-card
                              dark
                              flat
                              color="success"
                              class="px-2 d-flex justify-center align-center"
                            >
                              FRONT
                            </v-card>
                            <v-card flat class="pa-2 px-4">
                              {{ item }}
                            </v-card>
                          </v-card>
                          <v-card
                            v-else-if="index == data.back"
                            class="d-flex mb-3 mr-3"
                          >
                            <v-card flat class="pa-2 px-4">
                              {{ item }}
                            </v-card>
                            <v-card
                              color="error"
                              dark
                              flat
                              class="px-2 d-flex justify-center align-center"
                            >
                              BACK
                            </v-card>
                          </v-card>
                          <v-card v-else class="pa-2 mb-3 mr-3 px-4">
                            {{ item || "-" }}
                          </v-card>
                        </v-card>
                      </v-card>
                      <v-divider class="mb-3"></v-divider>
                    </div>
                  </template>
                </v-card>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
      </v-card>
    </v-content>
  </v-app>
</template>

<script>
import { CircularQueue } from "./structures";

export default {
  name: "App",
  data: () => ({
    tab1: "playin-tab",
    queue: new CircularQueue(10),
    maxQueueSize: 10,
    tab2: null,
    item: null,
    output: [],
    maxOutputSize: 10
  }),
  methods: {
    createQueue() {
      if (this.maxQueueSize !== this.queue.maxSize) {
        this.queue = new CircularQueue(this.maxQueueSize);
        this.output.push({
          operation: `created queue of size ${this.maxQueueSize}`
        });
      }
    },
    dequeue() {
      try {
        this.output.push({
          operation: "dequeue",
          result: this.queue.dequeue()
        });
      } catch (err) {
        this.output.push({
          operation: "dequeue()",
          result: err,
          isError: true
        });
      } finally {
        this.regulateOutput();
      }
    },
    peek() {
      try {
        this.output.push({ operation: "peek()", result: this.queue.peek() });
      } catch (err) {
        this.output.push({
          operation: "peek()",
          result: err,
          isError: true
        });
      } finally {
        this.regulateOutput();
      }
    },
    enqueue() {
      if (!this.item) return;
      try {
        this.queue.enqueue(this.item);
        this.output.push({
          operation: `enqueue(${this.item})`
        });
      } catch (err) {
        this.output.push({
          operation: "enqueue()",
          result: err,
          isError: true
        });
      } finally {
        this.item = null;
        this.regulateOutput();
      }
    },
    size() {
      this.output.push({ operation: "size()", result: this.queue.size() });
      this.regulateOutput();
    },
    isEmpty() {
      this.output.push({
        operation: "isEmpty()",
        result: this.queue.isEmpty()
      });
      this.regulateOutput();
    },
    isFull() {
      this.output.push({
        operation: "isFull()",
        result: this.queue.isFull()
      });
      this.regulateOutput();
    },
    allItems() {
      this.output.push({
        operation: "allItems()",
        result: [...this.queue.getItemsAsArray()],
        front: this.queue.front,
        back: this.queue.back,
        isEmpty: this.queue.isEmpty()
      });
      this.regulateOutput();
    },
    regulateOutput() {
      if (this.output.length > this.maxOutputSize) {
        this.output.shift();
      }
    }
  }
};
</script>

