export class Stack {
  constructor() {
    this.items = [];
  }

  size() {
    return this.items.length;
  }

  pop() {
    if (!this.isEmpty()) return this.items.pop();
    throw new Error("Underflow error");
  }

  push(item) {
    this.items.push(item);
  }

  isEmpty() {
    return this.items.length == 0;
  }

  peek() {
    if (!this.isEmpty()) return this.items[this.items.length - 1];
    throw new Error("Empty Stack");
  }

  getItemsAsArray() {
    return this.items;
  }
}

export class Queue {
  constructor(maxSize) {
    this.items = [];
    this.maxSize = maxSize;
  }

  enqueue(item) {
    if (!this.isFull()) return this.items.unshift(item);
    throw new Error("Overflow error");
  }

  dequeue() {
    if (!this.isEmpty()) return this.items.pop();
    throw new Error("Underflow error");
  }

  peek() {
    if (!this.isEmpty()) return this.items[this.items.length - 1];
    throw new Error("Empty Queue");
  }

  size() {
    return this.items.length;
  }

  isEmpty() {
    return this.items.length == 0;
  }

  isFull() {
    return this.size() == this.maxSize;
  }

  getItemsAsArray() {
    return this.items;
  }
}

export class CircularQueue {
  constructor(s) {
    this.maxSize = s;
    this.items = [];
    this.front = 0;
    this.back = -1;
    this.queueSize = 0;

    for (let i = 0; i < s; i++) {
      this.items[i] = null;
    }
  }

  enqueue(item) {
    if (this.isFull()) throw new Error("Overflow error");

    if (this.back == this.maxSize - 1) this.back = -1;
    this.items[++this.back] = item;
    this.queueSize++;
  }

  dequeue() {
    if (this.isEmpty()) throw new Error("Underflow error");

    let temp = this.items[this.front++];
    this.items[this.front - 1] = null;
    if (this.front == this.maxSize) this.front = 0;
    this.queueSize--;
    return temp;
  }

  peek() {
    if (this.isEmpty()) throw new Error("Empty Queue");

    return this.items[this.front];
  }

  isEmpty() {
    return this.queueSize == 0;
  }

  isFull() {
    return this.queueSize == this.maxSize;
  }

  size() {
    return this.queueSize;
  }

  getItemsAsArray() {
    return this.items;
  }
}

export class SLLNode {
  constructor(value, next_node) {
    this.value = value;
    this.next = next_node;
  }
}

export class DLLNode {
  constructor(value, next_node, previous_node) {
    this.value = value;
    this.next = next_node;
    this.previous = previous_node;
  }
}

export class SLinkedList {
  constructor(head) {
    this.head = head;
  }
}
